

  
  
  .navigationMenu {
    // margin-left: 200px;
    width: 100%;
    display: flex;
    justify-content: center;

  }
  nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    position: relative;
    display: flex;
    justify-content: center;
    gap: 100px;
    }
    
  nav ul li {
    display:inline-block;
    background-color: #ffffff;
    }
  
  nav a {
    display:block;
    padding:0 10px; 
    color:#000000;
    // font-size:17px;
    line-height: 50px;
    text-decoration:none;
    font-weight:300 ;
    font-family:Arial, Helvetica, sans-serif;
  }
    
  // nav ul li:hover > ul {
  //   display:inherit;
  // }
    
  nav ul ul li {
    width:170px;
    float:none;
    display:list-item;
    position: relative;
  }
  
  nav ul ul ul li {
    position: relative;
    top:-60px; 
    left:170px;
  }
  
  
  // li > a:after { content:  ' '; }
  li > a:only-child:after { content: ''; }
  
.is-sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  box-shadow: 0 2px 24px 0 rgb(0 0 0 / 15%);
  background-color: #ffffff !important;
  animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
  padding-top: 0px;
  padding-bottom: 0px;
}

.header-section {
  display: flex;
  width: 100%;
  // position: relative;
}
