@import url('https://fonts.googleapis.com/css2?family=Didact+Gothic&display=swap');

.wrapper {
    width: 100%;
    background: #ffffff;
    padding: 30px 0 60px;
    position: relative;
    transition: .5s;
    font-family: 'Didact Gothic', sans-serif;

}

.wrapperItem {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 100px;
    width: 90%;
}
.side1{
    width: 49%; 
    & img {
        width: 250px;
        height: 120px;
    }
}
.side1 p {
    font-size: 12px;    
    color: #6C7074;
    margin-left: 25px;
    line-height: 1.4;
    margin-bottom: 50px;
}
.block1 {
    // width: 50%;  
    color: #191919;
    // margin-top: 50px;
    max-width: 480px;    
    & h3 {
        // width: 500px;
line-height: 1.1;
    letter-spacing: 0.0075em;
    margin-bottom: 19px;
    font-size: 50px;
    font-weight: 400;
    
        
    }    
    & .block1p {
            color: #191919;
    // width: 30%;
    font-size: 20px;
    font-weight: 100;
    letter-spacing: 0.0075em;
    line-height: 1.4;
    margin-left: 0;
        }
    
}
.Slider1 {
        display: none;
    }
.side2{
    width: 35%;
    position: relative;
    // padding-bottom: 100px;
    & .Slider2 {
        margin-top: 100px;
    }
    
}

.button {
    margin-top: 30px;
    text-align: center;
    display: table;
    border-radius: 5px;
    background: #4800e8;
    border-radius: 6px;
    color: #fff;
    font-weight: 500;
    padding: 14px;
    text-decoration: none;
    width: 50%;
    margin-bottom: 50px;
    &:hover{
    background: #3c02b8;
    }
}


.telHeader {
    -webkit-text-size-adjust: 100%;
    box-sizing: border-box;
    background-color: transparent;
    outline: 0;
    text-decoration: none;
    color: #191919;
    // font-family: "TT Firs Neue", sans-serif;
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
    // margin-right: 0px;
    position: absolute;
    right: 100px;
    &:hover {
        color: #4800e8;
    }
}
.tel {
    display: flex;
    flex-direction: column;
    position: relative;
    right: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}
.icons {
    // margin-left: 330px;  
    display: flex;
    gap: 10px;
    font-size: 30px;
    margin-top: 10px;
    position: absolute;
    right: 100px;
    top: 15px;
}


@media only screen and (max-width: 943px){
.wrapperItem {
    max-width: 85%;
    display: flex;
    justify-content: center;
}
    .Slider1{
        display: block;
    }
    .Slider2{
        display: none;
    }
    .side1{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        width: 90%;
    }
    .tel {
        margin-left: -100px;
    }
}